.user {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 30px;
    width: 250px;
    height: 250px;
    background-color: #ffffff;
}

.user button {
    background-color: #088dcd;
    color: #ffffff;
    border: none;
    font-size: 13px;
    font-weight: 500;
    padding: 4px 20px;
    border-radius: 4px;
}

.user button:hover {
    cursor: pointer;
    background-color: #086393;
}

.user button:disabled {
    opacity: 0.5;
}

.name {
    padding: 10px;
}

.img {
    width: 140px;
    height: 140px;
    border-radius: 50%;
}

.avatar {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}